import React, { useEffect } from 'react';
import Layout from '../components/Layout';
import Loadable from '../components/Loadable';
import Link from '../components/Link';
import { getBlogTotal } from '../apis';
import styles from './blog.less';

const Blog = (props) => {
  const [{data, code}, getData] = getBlogTotal(data => data.items.map(({title, number}) => ({
    title, number
  })));
  useEffect(() => {
    getData();
  }, []);

  return (
    <Layout isLight title="博文">
      <Loadable loading={code !== 2}>
        <ul>
          {data && data.map(({title, number}) => (
            <li key={number} className={styles['blog-item']}>
              <Link
                href='/post/[id]'
                as={`/post/${number}`}
                text={title}
              />
            </li>
          ))}
        </ul>
      </Loadable>
    </Layout>
  )
}

